import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { DataTableModule } from 'ng2-data-table';
import { AppComponent } from './app.component';
import { LablinkComponent } from './lablink/lablink.component';
import { LoginComponent } from './login/login.component';
import { RouterModule } from '@angular/router';
import { LablinkService } from './service/lablink.service';
import { HttpModule } from '@angular/http';
import { MyDatePickerModule } from 'mydatepicker';
import {ToasterModule} from 'angular2-toaster';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

@NgModule({
  declarations: [
    AppComponent,
    LablinkComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,MyDatePickerModule,ToasterModule,BrowserAnimationsModule,FormsModule,DataTableModule,HttpModule,RouterModule.forRoot([
      {path:'', redirectTo:'/login', pathMatch:'full'},
      { path: 'login', component: LoginComponent},
       { path: 'lablink', component: LablinkComponent},
       { path: 'lablink/:welcomeUserName', component: LablinkComponent},
       {path: '***', redirectTo: '/login', pathMatch: 'full'}
  ], {useHash: true})
],

  providers: [LablinkService],
  bootstrap: [AppComponent]
})
export class AppModule { }



