import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Users } from '../domain/User';

@Injectable()
export class LablinkService {
    // url = 'http://172.17.1.40:8091/';
    url = '';
    constructor(private http: Http) {}

    public authenticateUser(user: Users) {
        const url = this.url + 'authenticateUser';
        const header = new Headers({ 'Content-Type': 'application/json' });
        const body = JSON.stringify(user);
        return this.http.post(url, body, { headers: header });
      }

      public getPatientInformation() {
        const url = this.url + 'patientDetails';
        const header = new Headers({ 'Content-Type': 'application/json' });
        return this.http.get(url);
      }

      public getMachineInformation() {
        const url = this.url + 'machineList';
        const header = new Headers({ 'Content-Type': 'application/json' });
        return this.http.get(url);
      }
      // tslint:disable-next-line:max-line-length
      public getPatientInformatiomBySampleNumberAndMachineName (sampleNumber: String, machineName: String , selectedDate: String, selectedSentDate: String) {
        const url =
          this.url +
          'lablink?sampleNumber=' +
          sampleNumber +
          '&machineName=' +
          machineName   + '&selectedDate=' + selectedDate + '&selectedSentDate=' + selectedSentDate; 
       console.log(url)
          const header = new Headers({ 'Content-Type': 'application/json' });
        return this.http.get(url);
      }
}


