import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { LabOrderItem } from '../domain/LabOrderItem';
import { LablinkService } from '../service/lablink.service';
import { IMyDpOptions, IMyDateModel } from 'mydatepicker';
import { ToasterConfig, ToasterService } from 'angular2-toaster';
import { Router } from '@angular/router';
import { Users } from '../domain/User';


@Component({
  selector: 'app-lablink',
  templateUrl: './lablink.component.html',
  styleUrls: ['./lablink.component.css'],
  providers:[ToasterService]
})
export class LablinkComponent implements OnInit {



public labOrderItem: LabOrderItem = new LabOrderItem();
public labOrderItemList: LabOrderItem[] = [];
public machineList: any[] = [];
public selectedMachine: any;
public selecteCreatedDate: any;
public currentSelectedDate = '';
public selectedSentDate: any;
public date = new Date();
public isUploadedDate: Boolean = false;


public myDatePickerOptions: IMyDpOptions =  {
  dateFormat: 'dd-mm-yyyy',
  disableSince: {year: this.date.getFullYear() + 10, month: this.date.getMonth() + 1, day: this.date.getDate()}
  };

  public toasterconfig: ToasterConfig = new ToasterConfig({
    positionClass: 'toast-top-right',
    animation: 'fade'
  });
  constructor( public lablinkService: LablinkService,public router: Router, public toasterService: ToasterService) {
    this.getAllLiceseDetails();
    this.getAllMachineList();
   }

  ngOnInit() {  }

  // tslint:disable-next-line:use-life-cycle-interface
  ngAfterContentInit() {
    window.onpopstate = function(e) {
      window.history.forward();
    };
  }

  onDateChanged(event: IMyDateModel) {
  const currentSelectedDateFormat = event.formatted.split('-');
    if (currentSelectedDateFormat.length > 2) {
      this.currentSelectedDate = currentSelectedDateFormat[2] + '-' + currentSelectedDateFormat[1] + '-' + currentSelectedDateFormat[0];
    } else {
      this.currentSelectedDate = '';
    }
  }

  public getAllLiceseDetails() {
    this.lablinkService.getPatientInformation().subscribe(
      data => {
        this.labOrderItemList = JSON.parse(JSON.parse(JSON.stringify(data))._body);
      },
      error => {
        console.log('Error : ' + error);
      }
    );
  }

  public getAllMachineList() {
    this.lablinkService.getMachineInformation().subscribe(
      data => {
        this.machineList = JSON.parse(JSON.parse(JSON.stringify(data))._body);
        this.selectedMachine = this.machineList[0];
      },
      error => {
        console.log('Error : ' + error);
      }
    );
  }

  public searchPatientInformation() {
    let currentSelectedMachine = this.selectedMachine;
    if (this.selectedMachine === 'All') {
      currentSelectedMachine = '';
    }

    if ((this.labOrderItem.sampleNumber) === undefined) {
      this.labOrderItem.sampleNumber = '';
    }

    if (this.isUploadedDate) {
      this.selectedSentDate = this.currentSelectedDate;
      this.selecteCreatedDate = '';
    } else {
      this.selecteCreatedDate = this.currentSelectedDate;
      this.selectedSentDate = '';
    }
    this.lablinkService
      .getPatientInformatiomBySampleNumberAndMachineName(
        this.labOrderItem.sampleNumber,
        currentSelectedMachine,
        this.selecteCreatedDate,
        this.selectedSentDate
      )
      .subscribe(
        data => {
          this.labOrderItemList = JSON.parse(JSON.parse(JSON.stringify(data))._body); 
          // console.log( this.labOrderItemList);       
          if(this.labOrderItemList.length==0)
          {
            this.toasterService.pop('warning', '', 'No Test prensent');
          }
        },
        error => {
          console.log(error);
        }
      );
  }

  myEvent(event) {
    if (event.key === 'Enter') {
    this.searchPatientInformation();
    }
  }

  public uploadedDateCheckbox(event) {
    if (event.checked) {
      this.isUploadedDate = true;
    } else {
      this.isUploadedDate = false;
    }
  }


  logoutAction() {
    this.router.navigateByUrl('/login');
  }
}



